<template>
    <main-layout>
        <div class="w-full md:grid md:grid-cols-4 md:mt-28 mt-5 ">
            <div class="col-span-1 md:mx-6">
                <form class="bg-white shadow-md rounded py-8 px-4 md:mr-6" @submit.prevent="onSubmit">
                    <p class="mb-5 text-3xl">Form User</p>
                    <div class="mb-4">
                        <label class=" label-custom ">
                            Nama
                        </label>
                        <input class="input-custom" v-model="form.nama" id="nama" type="text" >
                    </div>
                    <div class="mb-4">
                        <label class="label-custom">
                            Email
                        </label>
                        <input class="input-custom" v-model="form.email" id="email" type="email" >
                    </div>
                    <div class="mb-4">
                        <label class="label-custom">
                            No Telp
                        </label>
                        <input class="input-custom" v-model="form.no_telp" id="no_telp" type="text" >
                    </div>
                    <div class="mb-4">
                        <label class="label-custom">
                            Register Date
                        </label>
                        <input class="input-custom" v-model="form.register_date" id="register_date" type="date" >
                    </div>
                    <div class="">
                        <button 
                            type="submit" 
                            class=" btn-send ">
                            Send
                        </button>
                        <button 
                            @click="resetForm"
                            type="button" 
                            class=" btn-cancel ">
                            Cancel
                        </button>
                        <span >{{loading ? "loading" : ""}}</span>
                    </div>
                </form>
            </div>
            <div class="w-full col-span-3 md:mr-5">
                <div class="bg-white shadow-md rounded py-8 px-4 overflow-x-auto ">
                    <p class="mb-5 text-3xl">Data User</p>
                    <table class="shadow-lg bg-white w-full table-auto">
                        <tr>
                            <th class="text-white border text-center md:px-0 px-5 py-4" width="100" style="background-color: #4680FE">Action</th>
                            <th class="text-white border text-center px-8 py-4" style="background-color: #4680FE">Nama</th>
                            <th class="text-white border text-center px-8 py-4" style="background-color: #4680FE">Email</th>
                            <th class="text-white border text-center px-8 py-4" style="background-color: #4680FE">No Telp</th>
                            <th class="text-white border text-center px-8 py-4" style="background-color: #4680FE">Register Date</th>
                        </tr>
                        <tr v-if="loading">
                            <td  class=" text-center " colspan="5">Loading</td>
                        </tr>
                        <tr v-for="(item, index) in table.data"  :key="index">
                            <td class="border px-5 py-4 text-xs">
                                <img @click="onEdit(item.pid_user)" width="20" style="display: inline" class=" cursor-pointer " src="data:image/png;base64,"/>
                                <img @click="onRemove(item.pid_user)" width="20" style="display: inline" class="float-right cursor-pointer "  src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABgAAAAYCAYAAADgdz34AAAABmJLR0QA/wD/AP+gvaeTAAAAZElEQVRIiWNgGOqAkRhFP428Ghj/M9Qji/1n+N/Jfn57BckW/DL0+k+8+zAB2/ltKGYyUWLYyABYI5nceEAPfwYGOsQBQQvYzm9jxOYyXOIkW0ApGLVg1IJRC+hgwdAv7IY+AABHeRpR7gJWRgAAAABJRU5ErkJggg=="/>
                            </td>
                            <td class="border px-5 py-4 text-xs">{{item.nama}}</td>
                            <td class="border px-5 py-4 text-xs">{{item.email}}</td>
                            <td class="border px-5 py-4 text-xs">{{item.no_telp}}</td>
                            <td class="border px-5 py-4 text-xs">{{item.register_date}}</td>
                        </tr>
                    </table>
                </div>
            </div>
        </div>
    </main-layout>
</template>

<style lang="postcss" scoped>
    .btn-send {
        @apply bg-green-500 text-white border-2 font-bold py-2 px-4 rounded-lg
                hover:bg-green-700 hover:text-white 

    }
    .btn-cancel {
        @apply bg-white text-red-500 border-2 font-bold py-2 px-4 float-right rounded-lg           
                hover:bg-red-500 hover:text-white
    }
</style>

<script>
import { http } from '@/http.js';
import moment from 'moment';
import MainLayout from '@/pages/MainLayout';

const initialState = () => {
    return {
        pid_user: null,
        nama: null,
        email: null,
        no_telp: null,
        register_date: null,
    };
}
export default {
    data(){
        return {
            loading: false,
            form: initialState(),
            table: {},
            request: {
                offset : 0,
                limit: 10,
                search: null
            },            
        };
    },
    components: {
        MainLayout,
    },
    mounted() {
        this.getData();
    },
    created() {
        this.form.register_date = moment().format('YYYY-MM-DD');
    },
    methods: {
        async getData() {
            let that = this;
            this.loading = true;
            this.table = {};

            await http("api/user", this.request)
                        .then(function ({data}) {

                            if(data.status) {
                                that.table = data.data;
                            }

                            that.loading = false;
                        })
                        .catch(error => {
                            console.log(error);
                        });
        },
        async onSubmit() {
            let that = this;
            this.loading = true;

             if (this.form.nama == null || this.form.email == null || this.form.no_telp == null) {
                this.loading = false;
                this.$swal.mixin({
                        toast: true,
                        position: "top-end",
                        showConfirmButton: false,
                        timerProgressBar: true,
                        timer: 2000,
                    })
                    .fire({
                        icon: "warning",
                        title: "Maaf, form todak boleh kosong",
                    });
                    
                return false;
            }

            await http("api/user/store", this.form)
                    .then(function (responses) {
                        let data = responses.data;

                        if(data.status != undefined && data.status) {
                            that.$swal.mixin({
                                toast: true,
                                position: "top-end",
                                showConfirmButton: false,
                                timerProgressBar: true,
                                timer: 2000,
                            })
                            .fire({
                                icon: "success",
                                title: data.remark
                            });
                        }else if(data.status != undefined && !data.status) {
                            that.$swal.mixin({
                                toast: true,
                                position: "top-end",
                                showConfirmButton: false,
                                timerProgressBar: true,
                                timer: 2000,
                            })
                            .fire({
                                icon: "warning",
                                title: data.remark
                            });

                            console.log(data.remark);
                        }
                    })
                    .catch(error => {
                        console.log(error);
                    })
                    .finally(() => {
                        this.form = initialState();
                        this.loading = false;
                        this.getData();
                    });
        },
        async onRemove(pid_user) {    
            let that = this;        
            let foundData = this.table.data.filter(item => item.pid_user == pid_user)[0];

            return await this.$swal.fire({
                icon: 'question',
                title: `Do you want to delete user <b>${foundData.nama}</b> ?`,
                showDenyButton: true,
                confirmButtonText: 'Yes',
                denyButtonText: `No`,
            }).then((result) => {
                /* Read more about isConfirmed, isDenied below */
                if (result.isConfirmed) {

                    http("api/user/delete", foundData)
                        .then(function (responses) {
                            let data = responses.data;

                            if(data.status != undefined && data.status) {
                                that.$swal.mixin({
                                    toast: true,
                                    position: "top-end",
                                    showConfirmButton: false,
                                    timerProgressBar: true,
                                    timer: 2000,
                                })
                                .fire({
                                    icon: "success",
                                    title: data.remark
                                });

                                that.getData();
                            }else if(data.status != undefined && !data.status) {
                                that.$swal.mixin({
                                    toast: true,
                                    position: "top-end",
                                    showConfirmButton: false,
                                    timerProgressBar: true,
                                    timer: 2000,
                                })
                                .fire({
                                    icon: "warning",
                                    title: data.remark
                                });

                                console.log(data.remark);
                            }
                        })
                        .catch(error => {
                            console.log(error);
                        })
                } else if (result.isDenied) {
                    this.$swal.fire('Changes are not saved', '', 'info')
                }
            })
        },
        onEdit(pid_user) {   
            // let that = this;        
            let foundData = this.table.data.filter(item => item.pid_user == pid_user)[0];

            this.form.pid_user = foundData.pid_user;
            this.form.nama = foundData.nama;
            this.form.email = foundData.email;
            this.form.no_telp = foundData.no_telp;
            this.form.register_date =  moment(foundData.register_date).format('YYYY-MM-DD');
        },
        resetForm() {
            this.form = initialState();
        }
    }
}
</script>