<template>
  <main-layout>
    <modal
      v-bind:open="open"
      v-bind:getLast="getLast"
      v-bind:sortedBy="request.sorted_by"
      @update:sortedBy="request.sorted_by = $event"
    />
    <div v-if="list.length <= 0 && !loading" class="bg-white h-screen flex">
      <div class="text-center m-auto">
        <img
          class="m-auto"
          src="data:image/png;base64,"
        />
        <p class="">Data Empty</p>
      </div>
    </div>
    <div v-if="list.length <= 0 && loading" class="mt-28 mx-6 ">
      <div v-for="index in 12" :key="index" class="border border-gray-300 shadow rounded-md p-4 max-w-sm w-full inline-block m-4 ">
        <div class="animate-pulse flex space-x-4">
          <div class="flex-1 space-y-6 py-1">
            <div v-for="index in 3" :key="index" class="space-y-3">
              <div class="grid grid-cols-3 gap-4">
                <div class="h-2 bg-white rounded col-span-2"></div>
                <div class="h-2 bg-white rounded col-span-1"></div>
              </div>
              <div class="h-2 bg-white rounded"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
    
    <!-- main card -->
    <div
      v-if="list.length > 0"
      class="grid md:grid-cols-4 md:gap-5 md:mx-10 md:my-6 mt-12 md:mt-20"
    >
      <div
        v-for="(item, index) in list"
        :id="'card_' + index"
        :key="index"
        @click="capture(index)"
        class="
          max-w-sm
          overflow-hidden
          mb-2
          bg-white
          rounded-lg
          transform
          duration-200
          cursor-pointer
        "
      >
        <div class="mx-6 border-b grid bg-white">
          <div class="flex my-4">
            <div class="flex-1">
              <div class="grid">
                <div class="text-lg font-bold">
                  {{ item.account }}
                </div>
                <div class="text-green-600">
                  {{ !hiddenName ? sortName(item.account_name) : "*****" }}
                </div>
              </div>
            </div>
            <div class="flex-1">
              <div class="text-right">
                <!-- pnlday -->
                <div class="font-bold inline-block text-lg">
                  {{ item.ea_enable == 1 ? "Active" : "Non-Active" }}
                  <div
                    :id="'location_' + index"
                    class="
                      text-xs
                      font-semibold
                      inline-block
                      text-white
                      pt-1
                      pb-1
                      px-1
                      bg-green-500
                      rounded-sm
                    "
                  >
                    {{ item.location }}
                  </div>
                </div>
                <div class="text-xs text-right mt-1 ">
                  <!-- Aync: 17 Oct 21 15:09 -->
                  Sync: {{ formatDate(item.broker_time) }}
                </div>
              </div>
            </div>
          </div>
          <div class="flex my-4">
            <div class="flex-1">
              <div class="grid">
                <div class="">
                  <strong style="display: inline"> Floating </strong>
                  <p class="text-sm text-blue-500 font-bold inline-block">
                    {{ item.dd }}%
                  </p>
                </div>
                <div class="text-red-500 font-bold text-2xl">
                  {{ item.floating }}
                </div>
              </div>
            </div>
            <div class="flex-1">
              <div class="grid text-right">
                <!-- pnlday -->
                <div class="font-bold">
                  <p
                    class="text-sm text-blue-500 font-bold"
                    style="display: inline"
                  >
                    {{ item.pnlday }}%
                  </p>
                  <strong style="display: inline"> Profit/Day </strong>
                </div>
                <div
                  :class="colorProfit(item.profit)"
                  class="font-bold text-2xl"
                >
                  {{ item.profit }}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          :class="bgFooter(item.ea_enable)"
          class="flex text-center text-white py-2"
        >
          <div class="flex-1">
            <div class="grid">
              <div class="text-lg font-bold">
                {{ item.trade }}
              </div>
              <div class="text-sm">Open</div>
            </div>
          </div>
          <div class="flex-1">
            <div class="grid">
              <div class="text-lg font-bold">
                {{ item.custom_equity }}
              </div>
              <div class="text-sm">Equity USC</div>
            </div>
          </div>
          <div class="flex-1">
            <div class="grid">
              <div class="text-lg font-bold">
                {{ item.pnlmnt }}%
              </div>
              <div class="text-sm">PnL Month</div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <button
      type="button"
      @click="hiddenName = !hiddenName"
      class="
        bg-white
        border-4 border-green-300
        text-white
        font-bold
        py-2
        px-2
        rounded-lg
        focus:outline-none focus:shadow-outline
        btn-hidden-name
      "
    >
      <svg
        v-if="!hiddenName"
        xmlns="http://www.w3.org/2000/svg"
        fill="currentColor"
        class="bi bi-eye text-green-300"
        width="20"
        viewBox="0 0 16 16"
      >
        <path
          d="M16 8s-3-5.5-8-5.5S0 8 0 8s3 5.5 8 5.5S16 8 16 8zM1.173 8a13.133 13.133 0 0 1 1.66-2.043C4.12 4.668 5.88 3.5 8 3.5c2.12 0 3.879 1.168 5.168 2.457A13.133 13.133 0 0 1 14.828 8c-.058.087-.122.183-.195.288-.335.48-.83 1.12-1.465 1.755C11.879 11.332 10.119 12.5 8 12.5c-2.12 0-3.879-1.168-5.168-2.457A13.134 13.134 0 0 1 1.172 8z"
        />
        <path
          d="M8 5.5a2.5 2.5 0 1 0 0 5 2.5 2.5 0 0 0 0-5zM4.5 8a3.5 3.5 0 1 1 7 0 3.5 3.5 0 0 1-7 0z"
        />
      </svg>
      <svg
        v-if="hiddenName"
        xmlns="http://www.w3.org/2000/svg"
        fill="currentColor"
        class="bi bi-eye-slash-fill text-green-300"
        width="20"
        viewBox="0 0 16 16"
      >
        <path
          d="m10.79 12.912-1.614-1.615a3.5 3.5 0 0 1-4.474-4.474l-2.06-2.06C.938 6.278 0 8 0 8s3 5.5 8 5.5a7.029 7.029 0 0 0 2.79-.588zM5.21 3.088A7.028 7.028 0 0 1 8 2.5c5 0 8 5.5 8 5.5s-.939 1.721-2.641 3.238l-2.062-2.062a3.5 3.5 0 0 0-4.474-4.474L5.21 3.089z"
        />
        <path
          d="M5.525 7.646a2.5 2.5 0 0 0 2.829 2.829l-2.83-2.829zm4.95.708-2.829-2.83a2.5 2.5 0 0 1 2.829 2.829zm3.171 6-12-12 .708-.708 12 12-.708.708z"
        />
      </svg>
    </button>
    <button
      type="button"
      @click="onToggleModal"
      class="
        bg-white
        border-4 border-green-300
        text-white
        font-bold
        py-2
        px-2
        rounded-lg
        focus:outline-none focus:shadow-outline
        btn-orderBy
      "
    >
      <img
        width="20"
        src="data:image/png;base64,"
      />
    </button>
    <button
      type="button"
      @click="refresh"
      class="
        bg-white
        border-4 border-green-300
        text-white
        font-bold
        py-2
        px-2
        rounded-lg
        focus:outline-none focus:shadow-outline
        btn-refresh
      "
    >
      <img
        width="20"
        v-if="!loading"
        class="transition-all duration-300"
        src="data:image/png;base64,"
      />
      <img
        style="display: inline"
        v-if="loading"
        width="20"
        class="transition-all duration-300"
        src="data:image/png;base64,"
      />
    </button>
  </main-layout>
</template>

<style lang="scss">
.btn-hidden-name {
  position: fixed;
  right: 15px;
  bottom: 120px;
}

.btn-orderBy {
  position: fixed;
  right: 15px;
  bottom: 75px;
}

.btn-refresh {
  position: fixed;
  right: 15px;
  bottom: 30px;
}
</style>

<script lang="js" src="./DashboardController.js"></script>
